@mixin box-shadow() {
    -webkit-box-shadow: $box-shadow;
    box-shadow: $box-shadow;
  }
  
@mixin transition() {
    -webkit-transition: $webkit-transition;
    transition: $transition;
}

@mixin heightEWidth($propertyHeight, $propertyWidth){
  height: $propertyHeight;
  width: $propertyWidth;
}

@mixin opacidade($propriedade){
  opacity: $propriedade;
}