.cd-top {
  display: inline-block;
  height: 40px;
  width: 40px;
  position: fixed;
  bottom: 40px;
  right:10px;
  @include box-shadow();
  @include transition();
  overflow: hidden;
  text-indent: 100%;
  white-space: nowrap;
  background:#ffa69e url(../img/cd-top-arrow.svg) no-repeat center 50%;
  visibility: hidden;
  opacity:0;
}

.cd-top.cd-top--show, .cd-top.cd-top--fade-out, .cd-top:hover {
  @include transition();
}

.cd-top.cd-top--show {
  visibility: $visible;
  @include opacidade(1);
}

.cd-top.cd-top--fade-out {
  @include opacidade(.5);
}

.cd-top:hover {
  background-color: $background-color;
  @include opacidade(1);
}

@media only screen and (min-width: 768px) {
  .cd-top {
    right: 20px;
    bottom: 20px;
  }
}

@media only screen and (min-width: 1024px) {
  .cd-top {
    height:60px;
    width:60px;
    right:30px;
    bottom:30px;
  }
}

